<template>
	<div>
		<div class="header">
			<div class="icon-left" @click="$router.goBack()"><img src="../../static/img/icon/release_icon/return@2x.png"/></div>
			<div>话题详情</div>
			<div>
				。。。
			</div>
		</div>
		<div class="header-null"></div>
		<div class="conversationDetail">
			<div class="conversationDetail-left">
				<img src="../../static/img/icon/find_icon/add.png"/>
			</div>
			<div class="conversationDetail-right">
				<div class="title">
					#十二卷、龙舌兰等及组培区#
				</div>
				<div class="content">
					十二卷、龙舌兰、芦荟
				</div>
				<div>0参与</div>
			</div>
		</div>
		<div class="classification">
			<div class="item" v-for="(item, index) in list">
				<div>{{item}}</div>
				<div v-if="index%2 == 0">/</div>
			</div>
		</div>
		<div class="list">
			<div class="item" v-for="item in data">
				<div class="item-header">
					<img src="../../static/img/icon/denglu_gengxin_icon/xilv_banbengengxin@3x.png"/>
				</div>
				<div class="item-title">
					写下日记准备学习种植花蕊
				</div>
				<div class="item-bottom">
					<div>
						<div>
							<img src="../../static/img/icon/my_icon/pinglun_luntan@3x.png"/>
							<div>1</div>
						</div>
					</div>
					<div>
						<div>
							<img src="../../static/img/icon/find_icon/collection@3x.png"/>
							<div>1</div>
						</div>
					</div>
					<div>
						<div>
							<img src="../../static/img/icon/my_icon/heart_luntan@3x.png"/>
							<div>1</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {getData} from '../service/getData'
	export default {
		data() {
			return {
				list: ['#123#', '#456#', '#789#','#asd#'],
				data: [1,1]
			}
		},
		methods: {
			init() {
				this.$store.state.isLoading = true
				getData('topic/appFindTopicById', {
					token: sessionStorage.token,
					topicId: this.$roue.params.id
				}).then(d => {
					this.$store.state.isLoading = false
				}).catch(err => {
					this.$store.state.isLoading = false
					console.log(err)
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style scoped lang="less">
	.conversationDetail {
		padding: 10px;
		display: flex;
		background: #fff;
		
		.conversationDetail-left {
			width: 140px;
			height: 80px;
			margin-right: 10px;
			
			img {
				width: 100%;
				height: 100%;
			}
		}
		
		.conversationDetail-right {
			
			.title {
				font-weight: bold;
				font-size: 15px;
				margin-bottom: 10px;
			}
			
			.content {
				font-size: 13px;
				margin-bottom: 10px;
			}
			
			div:last-child {
				border-radius: 5px;
				padding: 2px 20px;
				background: #e5e5e5;
				float: right;
			}
		}
	}
	
	.classification {
		display: flex;
		flex-wrap: wrap;
		margin-top: 10px;
		background: #fff;
		
		.item {
			width: 50%;
			padding: 10px 0 10px 10px;
			display: flex;
			justify-content: space-between;
		}
	}
	
	.list {
		display: flex;
		
		.item {
			width: 50%;
			margin: 5px;
			background: #fff;
			
			&:nth-child(even) {
				margin-left: 0;
			}
			
			.item-header {
				width: 100%;
				
				img {
					width: 100%;
					height: 100%;
				}
			}
			
			.item-title {
				font-weight: bold;
				font-size: 13px;
				margin: 5px;
			}
			
			.item-bottom {
				display: flex;
				height: 40px;
				justify-content: center;
				text-align: center;
				
				div {
					display: -webkit-box;
					align-items: center;
					flex: 1;
					
					div {
						display: flex;
						margin: 0 auto;
						align-items: center;
					}
					
					img {
						width: 15px;
						height: 15px;
						margin-right: 5px;
					}
				}
			}
		}
	}
</style>


